<template>
  <div class="main">
    <el-tabs value="1">
      <el-tab-pane label="" name="1" style="text-align:left">
        <div class="box">
          <el-form
            inline
            :model="form"
            label-width="145px"
            ref="Form"
            label-position="right"
            :rules="rules"
          >
            <div class="boxTitle"></div>
            <el-form-item label="">
              <el-input size="small" v-model="form.userName" disabled></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input type="number" size="small" v-model="form.userIdentity" disabled></el-input>
            </el-form-item>
            <el-form-item label="" prop="userSex" >
              <el-radio-group v-model="form.userSex">
                <el-radio label="" value=""></el-radio>
                <el-radio label="" value=""></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" prop="userBirthday" >
              <el-date-picker
                size="small"
                v-model="form.userBirthday"
                type="date"
                placeholder=""
                value-format="yyyy/MM/dd"
                format="yyyy  MM  dd "
              ></el-date-picker>
            </el-form-item>
            <el-form-item label="" prop="userMiddleSchool" >
              <el-input size="small" v-model="form.userMiddleSchool" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userPostalCode" label="" >
              <el-input size="small" v-model="form.userPostalCode" placeholder=""></el-input>
            </el-form-item>

            <div class="boxTitle"></div>
            <el-form-item label="">
              <el-input size="small" v-model="form.userMailBox" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userQq" label="QQ" >
              <el-input size="small" type="number" v-model="form.userQq" placeholder="QQ"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input size="small" v-model="form.userWeChat" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userFamilyPhone" label="" style="display:block" >
              <el-input size="small" type="number" v-model="form.userFamilyPhone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input
                size="small"
                style="width:705px"
                v-model="form.userPostalAddress"
                placeholder=""
              ></el-input>
            </el-form-item>

            <div class="boxTitle"></div>
            <el-form-item prop="userContactName" label="" >
              <el-input size="small" v-model="form.userContactName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userContactPhone" label="" >
              <el-input size="small" type="number" v-model="form.userContactPhone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userContactIdentity" label="" >
              <el-input size="small" type="number" v-model="form.userContactIdentity" placeholder=""></el-input>
            </el-form-item>
            <div class="boxTitle"></div>
            <el-form-item prop="userDepartment" label="" >
              <el-input size="small" v-model="form.userDepartment" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userMajor" label="" >
              <el-input size="small" v-model="form.userMajor" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userStudentId" label="">
              <el-input size="small" v-model="form.userStudentId" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="userMajorCategory" label="" >
              <el-select
                size="small"
                v-model="form.userMajorCategory"
                filterable
                placeholder=""
              >
                <el-option
                  v-for="(item,index) in userMajorCategoryList"
                  :key="index"
                  :label="item"
                  :value="item"
                ></el-option>
              </el-select>
            </el-form-item>

            <div class="boxTitle"></div>
            <el-form-item prop="changeReason" style="display:block" label="" >
              <el-input
                style="width:705px"
                size="small"
                type="textarea"
                :rows="2"
                v-model="form.changeReason"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item class="submit">
              <el-button type="primary" @click="submit"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="2" style="text-align:left">
        <div class="box">
          <el-form
            inline
            :model="form2"
            label-width="145px"
            ref="Form2"
            label-position="right"
            :rules="rules2"
          >
            <div class="boxTitle"></div>
            <el-form-item prop="guardianName" label="" >
              <el-input size="small" v-model="form2.guardianName" placeholder="" disabled></el-input>
            </el-form-item>
            <el-form-item type="number" prop="guardianIdentity" label="" >
              <el-input
                size="small"
                v-model="form2.guardianIdentity"
                placeholder=""
                disabled
              ></el-input>
            </el-form-item>
            <el-form-item
              prop="guardianPermanentAddress"
              label=""
              style="display:block"
              required
            >
              <div class="selectGround">
                <el-select
                  size="small"
                  v-model="form2.guardianProvinceId"
                  filterable
                  placeholder=""
                  @change="getCity('guardianProvinceId')"
                >
                  <el-option
                    v-for="item in provinceList"
                    :key="item.provinceId"
                    :label="item.provinceName"
                    :value="item.provinceId"
                  ></el-option>
                </el-select>
                <el-select
                  size="small"
                  v-model="form2.guardianCityId"
                  filterable
                  placeholder=""
                  @change="getArea('guardianCityId')"
                >
                  <el-option
                    v-for="item in cityList"
                    :key="item.cityId"
                    :label="item.cityName"
                    :value="item.cityId"
                  ></el-option>
                </el-select>
                <el-select
                  size="small"
                  v-model="form2.guardianAreaId"
                  filterable
                  placeholder=""
                >
                  <el-option
                    v-for="item in areaList"
                    :key="item.areaId"
                    :label="item.areaName"
                    :value="item.areaId"
                  ></el-option>
                </el-select>
              </div>
              <el-input
                style="width:705px"
                size="small"
                v-model="form2.guardianPermanentAddress"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item prop="guardianPostalCode" label="" >
              <el-input size="small" type="number" v-model="form2.guardianPostalCode" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="guardianHealth" label="" >
              <el-radio-group v-model="form2.guardianHealth">
                <el-radio label="" value=""></el-radio>
                <el-radio label="" value=""></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item prop="guardianTel" label="" >
              <el-input size="small" type="number" v-model="form2.guardianTel" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="changeReason" style="display:block;margin-top:10px" label="" >
              <el-input
                style="width:705px"
                size="small"
                type="textarea"
                :rows="2"
                v-model="form2.changeReason"
                placeholder=""
              ></el-input>
            </el-form-item>
            <el-form-item class="submit">
              <el-button type="primary" @click="submit2"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
export default {
  data() {
    return {
      accountCharacterList: [],
      fundingCenterList: [],
      provinceList: [],
      cityList: [],
      areaList: [],
      userEducationList: ["", "", "", "", ""],
      userEducationalSystemList: ["3", "4", "6", "8"],
      userMajorCategoryList: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      userFamousRaceList: [],
      form: {
        userName: "",
        userIdentity: "",
        userSex: "",
        userBirthday: "",
        userMiddleSchool: "",
        userPostalCode: "",

        userMailBox: "",
        userQq: "",
        userWeChat: "",
        userPostalAddress: "",
        userFamilyPhone: "",

        userContactName: "",
        userContactPhone: "",
        userContactIdentity: "",

        userDepartment: "",
        userMajor: "",
        userStudentId: "",
        userMajorCategory: "",
        changeReason: ""
      },
      form2: {
        guardianName: "",
        guardianIdentity: "",
        guardianProvinceId: "",
        guardianCityId: "",
        guardianAreaId: "",
        guardianPermanentAddress: "",
        guardianPostalCode: "",
        guardianHealth: "",
        guardianTel: "",
        changeReason: ""
      },
      rules: {
        userSex: [{ required: true, message: "", trigger: "blur" }],
        userBirthday: [
          { required: true, message: "", trigger: "blur" }
        ],
        userMiddleSchool: [
          { required: true, message: "", trigger: "blur" }
        ],
        userPostalCode: [
          { required: true, message: "", trigger: "blur" }
        ],

        userQq: [{ required: true, message: "QQ", trigger: "blur" }],
        userFamilyPhone: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (
                /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false
              ) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],

        userContactName: [
          { required: true, message: "", trigger: "blur" }
        ],
        userContactPhone: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (
                /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false
              ) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        userContactIdentity: [
          { required: true, message: "", trigger: "blur" },
          { min: 15, max: 18, message: "", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "",
            trigger: "blur"
          }
        ],

        userDepartment: [
          { required: true, message: "", trigger: "blur" }
        ],
        userMajor: [{ required: true, message: "", trigger: "blur" }],
        userMajorCategory: [
          { required: true, message: "", trigger: "blur" }
        ],
        changeReason: [
          { required: true, message: "", trigger: "blur" }
        ]
      },
      rules2: {
        guardianName: [
          { required: true, message: "", trigger: "blur" }
        ],
        guardianIdentity: [
          { required: true, message: "", trigger: "blur" },
          { min: 15, max: 18, message: "", trigger: "blur" },
          {
            required: true,
            pattern: /(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)|(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)/,
            message: "",
            trigger: "blur"
          }
        ],
        guardianPermanentAddress: [
          { required: true, message: "", trigger: "blur" }
        ],
        guardianPostalCode: [
          { required: true, message: "", trigger: "blur" }
        ],
        guardianHealth: [
          { required: true, message: "", trigger: "blur" }
        ],
        guardianTel: [
          { required: true, message: "", trigger: "blur" },
          {
            validator: function(rule, value, callback) {
              if (
                /^((0\d{2,3}-\d{7,8})|(1[34578]\d{9}))$/.test(value) == false
              ) {
                callback(new Error(""));
              } else {
                callback();
              }
            },
            trigger: "blur"
          }
        ],
        changeReason: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  mounted() {
    this.$axios
      .post("/user/getUserInfo", {
        userLoginAccount: sessionStorage.getItem("userLoginAccount")
      })
      .then(({ status, data }) => {
        if (status == 200) {
          const {
            userName,
            userIdentity,
            userSex,
            userBirthday,
            userMiddleSchool,
            userPostalCode,
            userMailBox,
            userQq,
            userWeChat,
            userPostalAddress,
            userFamilyPhone,
            userContactName,
            userContactPhone,
            userContactIdentity,
            userDepartment,
            userMajor,
            userStudentId,
            userMajorCategory
          } = data.user;
          this.form = {
            userName,
            userIdentity,
            userSex,
            userBirthday: userBirthday.substr(0, 10).replace(/-/g, "/"),
            userMiddleSchool,
            userPostalCode,
            userMailBox,
            userQq,
            userWeChat,
            userPostalAddress,
            userFamilyPhone,
            userContactName,
            userContactPhone,
            userContactIdentity,
            userDepartment,
            userMajor,
            userStudentId,
            userMajorCategory
          };
        }
      });
    this.$axios
      .post("/guardian/getGuardianInfo", {
        userLoginAccount: sessionStorage.getItem("userLoginAccount")
      })
      .then(({ status, data }) => {
        if (status == 200) {
          //       guardianProvinceId,
          // guardianCityId,
          const {
            guardianName,
            guardianIdentity,
            guardianAreaId,
            guardianPermanentAddress,
            guardianPostalCode,
            guardianHealth,
            guardianTel
          } = data.guardian;
          this.form2 = {
            guardianName,
            guardianIdentity,
            guardianAreaId,
            guardianPermanentAddress,
            guardianPostalCode,
            guardianHealth,
            guardianTel
          };
        }
      });
    this.$axios
      .post("/famousRace/getAllFamousRace")
      .then(({ status, data }) => {
        if (status == 200) {
          this.userFamousRaceList = data;
        }
      });
    this.$axios
      .post("/accountCharacter/getAllAccountCharacter")
      .then(({ status, data }) => {
        if (status == 200) {
          this.accountCharacterList = data;
        }
      });
    this.$axios
      .post("/fundingCenter/getFundingCenterList")
      .then(({ status, data }) => {
        if (status == 200) {
          this.fundingCenterList = data;
        }
      });
    this.$axios.post("/province/getAllProvince").then(({ status, data }) => {
      if (status == 200) {
        this.provinceList = data;
      }
    });
  },
  methods: {
    getCity(key) {
      this.$axios
        .post("/city/getAllCityByProvinceId", {
          provinceId: this.form2[key]
        })
        .then(({ status, data }) => {
          if (status == 200) {
            this.cityList = data;
          }
        });
    },
    getArea(key) {
      this.$axios
        .post("/area/getAllAreaByCityId", {
          cityId: this.form2[key]
        })
        .then(({ status, data }) => {
          if (status == 200) {
            this.areaList = data;
          }
        });
    },
    submit() {
      this.$refs.Form.validate(valid => {
        if (valid) {
          this.$axios
            .post("/user/updateUser", {
              userLoginAccount: sessionStorage.getItem("userLoginAccount"),
              ...this.form
            })
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "",
                  type: "success"
                });
              } else if (status == 401) {
                this.$message({
                  message: "",
                  type: "error"
                });
              }
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    },
    submit2() {
      this.$refs.Form2.validate(valid => {
        if (valid) {
          this.$axios
            .post("/guardian/updateGuardian", {
              userLoginAccount: sessionStorage.getItem("userLoginAccount"),
              ...this.form2
            })
            .then(({ status }) => {
              if (status == 200) {
                this.$message({
                  message: "",
                  type: "success"
                });
              } else if (status == 401) {
                this.$message({
                  message: "",
                  type: "error"
                });
              }
            })
            .catch(error => {
              console.log(error);
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
.main {
  min-width: 700px;
  max-width: 1000px;
  padding: 20px 35px;
  background: #fff;
  .box {
    padding: 15px 30px;
    .boxTitle {
      text-align: left;
      color: rgb(0, 158, 223);
      font-size: 16px;
      font-weight: bold;
    }
    .boxTitle:not(:first-child) {
      margin-top: 30px;
    }
    .el-form-item {
      margin-bottom: 13px;
      /deep/ .el-form-item__label {
        font-size: 14px;
        font-weight: bold;
      }
      /deep/ .el-form-item__content {
        width: 275px;
        text-align: left;
      }
      .selectGround {
        display: flex;
        width: 705px;
        /deep/ .el-select {
          margin-right: 10px;
        }
      }
    }
  }
  .submit {
    display: flex;
    justify-content: flex-end;
    margin-top: 20px;
    /deep/ .el-form-item__content {
      width: auto !important;
      text-align: right !important;
    }
  }
}
</style>